@Composable
fun ContactSection(
    showCloud:Boolean = true,
    onClick: () -> Unit
) {
    val screenWidth = screenWidth
    Box {

        Column(
            modifier = Modifier
                .background(MaterialTheme.colorScheme.surface)
                .padding(vertical = MaterialTheme.dimens.spacing.xxxLarge)
        ) {
            Spacer(modifier = Modifier.height(MaterialTheme.dimens.spacing.xxxLarge))

            Box(
                modifier = Modifier.fillMaxWidth()
            ) {
                Image(
                    modifier = Modifier
                        .size(40.dp)
                        .offset(
                            x = remember(screenWidth) { screenWidth * 0.065f },
                            y = -14.dp
                        ),
                    painter = painterResource(Res.drawable.whatsapp),
                    contentDescription = null,
                )
                Image(
                    modifier = Modifier
                        .size(24.dp)
                        .offset(
                            x = remember(screenWidth) { screenWidth * 0.56f },
                            y = -24.dp
                        ),
                    painter = painterResource(Res.drawable.whatsapp),
                    contentDescription = null,
                )
                Image(
                    modifier = Modifier
                        .size(48.dp)
                        .offset(x = remember(screenWidth) { screenWidth * 0.87f })
                        .rotate(20f),
                    painter = painterResource(Res.drawable.whatsapp),
                    contentDescription = null,
                    colorFilter = ColorFilter.tint(MaterialTheme.colorScheme.primary)
                )
            }
            Spacer(modifier = Modifier.height(8.dp))

            Row(
                modifier = Modifier.fillMaxWidth(),
                horizontalArrangement = Arrangement.Center,
                verticalAlignment = Alignment.CenterVertically
            ) {
                Text(
                    text = stringResource(Res.string.label_contact),
                    style = MaterialTheme.typography.headlineMedium
                )
                Spacer(modifier = Modifier.width(MaterialTheme.dimens.spacing.xxxLarge))
                PrimaryButton(
                    text = buildAnnotatedString {
                        withStyle(style = SpanStyle(fontWeight = FontWeight.Normal)) {
                            append(stringResource(Res.string.action_contact))
                        }
                        withStyle(SpanStyle(fontWeight = FontWeight.Bold)) {
                            append(stringResource(Res.string.whatsapp))
                        }
                    },
                    onPurchaseClick = onClick,
                    buttonColors = ButtonDefaults.buttonColors()
                        .copy(containerColor = MaterialTheme.colorScheme.secondary)
                )

            }
            Spacer(modifier = Modifier.height(MaterialTheme.dimens.spacing.xxLarge))
            Box(
                modifier = Modifier.fillMaxWidth(),
            ) {
                Image(
                    modifier = Modifier
                        .size(32.dp)
                        .offset(x = remember(screenWidth) { screenWidth * 0.3f }),
                    painter = painterResource(Res.drawable.whatsapp),
                    contentDescription = null,
                    colorFilter = ColorFilter.tint(MaterialTheme.colorScheme.primary)
                )
                Image(
                    modifier = Modifier
                        .size(24.dp)
                        .offset(
                            x = remember(screenWidth) { screenWidth * 0.92f },
                            y = -22.dp
                        )
                        .rotate(20f),
                    painter = painterResource(Res.drawable.whatsapp),
                    contentDescription = null,
                )
            }
        }
        if (showCloud) {
            Image(
                modifier = Modifier
                    .width(remember(screenWidth) { screenWidth * 0.327f })
                    .offset(y = -MaterialTheme.dimens.spacing.xxLarge*2),
                painter = painterResource(Res.drawable.cloud_9),
                contentDescription = null
            )
        }
    }
}